@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
    margin :0;
    padding: 0;
    font-family: 'Mulish', sans-serif;
}
.navbar{
    background-color: #fff !important;
}
.navbar-brand{
    font-size: 1.9rem !important;
    color: #565387 !important;
}
.nav-link{
    font-size:1.3rem;
}
.nav-link:hover{
    color : #3498db !important;
}
.menu-active{
    font-weight :bold;
    border-bottom: 1px solid #565387;
}


#header {
    width:100%;
    margin-top:50px;
    height: 75vh; 
}
#header h1{
    font-size: 48px;
    color:black;
}
#header h2{
    color :#484848;
    font-size: 24px;
}
#header .brand-name{
    color : #3498db;
}
#header .btn-get-started{
    font-size: 16px;
    letter-spacing: 1px;
    border :2px solid #3498db;
    text-decoration: none;
    transition: 0.5s;
    padding : 10px;
    border-radius: 50px;

}
#header .btn-get-started:hover{
    background-color: #3498db;
    color:#fff;
}
 .header-img{
   margin-top: 30px;       
}
.card-img-top{
    height :250px;
    width:443px;
}

@-webkit-keyframes up-down {
    0% {
        -webkit-transform : translateY(10px);
                transform : translateY(10px);
    }
    100% {
        -webkit-transform : translateY(-10px);
                transform : translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        -webkit-transform : translateY(10px);
                transform : translateY(10px);
    }
    100% {
        -webkit-transform : translateY(-10px);
                transform : translateY(-10px);
    }
}
 #header .animation{
   animation : up-down 2s ease-in-out infinite alternate-reverse both;
   border-radius: 20px;

}
@media(max-width:768px){
    .animation{
        position: relative;
        left :-140px;
        top:-50px;
    }
    #header h1{
        font-size:33px;
     }
     #header h2{
         font-size: 20px;
     }
     .card-img-top{
         width:270px;
     }

} 

